import React from "react";
import "./Auth.scss";
import ReactLoading from "react-loading";

interface AuthProps {}

const Auth = ({}: AuthProps) => {
  return (
    <>
      <div className="Auth">
        <div className="Auth-Container">
          <p className="Auth-Container-Title">  !</p>
          <p className="Auth-Container-Subtitle"> ... </p>
          <ReactLoading
            className="Auth-Container-Loading"
            type={"spokes"}
            height={"2.4rem"}
            width={"2.4rem"}
          />
        </div>
      </div>
    </>
  );
};

export default Auth;
